body {
  margin: auto, 0;
  padding: 0;
  background-color: #7255cf;
  width: 100vw;
  height: 100vh;
  background-repeat: repeat;
  font-family: 'Comfortaa', cursive;
  text-align: center;
}


.app {
  width: 100vw;
  height: 100vh;
}

header {
  position: absolute;
  width: 70%;
  left: 15%;
}
h1 {
  font-weight: bolder;
  font-size: 3em;
}

.aquaTxt {
  color: rgb(10, 222, 252);
}

.list {
  position: absolute;
  width: 70%;
  left: 15%;
  top: 275px; 
}

@media only screen and (max-width: 600px) {
    h1 {
        font-size: 1.5em;
    }
    p {
      font-size: 0.8em;
    }
}
.searchBar {
	padding: 5px;
	margin-bottom: 20px;
}

input {
	padding: 3px;
	background-color: #ffffff;
	opacity: 0.3;
	color: black;
	border: solid 2px rgb(0, 73, 91);
	border-radius: 5px;
	height: 38px;
	width: 250px;
	text-align: center;
	font-weight: 730;
}
.friend-card {
	text-align: center;
	display: inline-block;
	background-color: #ffffff;
	border-style: none;
	border-radius: 15px;
	padding: 30px;
	margin: 7px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	opacity: 0.4;
	transition: -webkit-transform 0.5s linear;
	transition: transform 0.5s linear;
	transition: transform 0.5s linear, -webkit-transform 0.5s linear;
}

.friend-card:hover {
	opacity: 0.8;
	cursor: pointer;
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

@media only screen and (max-width: 350px) {
    .robo-card {
        max-width: 250px;
    }
}
